<style lang="scss">@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");
:root {
  --color-blue: linear-gradient(rgb(84 136 255), rgb(38 99 186));
  --color-white: white;
  --color-gray: rgb(26, 26, 26);
  --color-green: #21b63a;
  --color-text: rgba(0, 0, 0, 0.849);
  --shadow: 0 0 55px 56px #0000002e; }

:global(html),
:global(body) {
  margin: 0 auto; }

:global(body) {
  min-width: 100vw;
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;
  background: var(--color-blue); }

:global(button),
:global(input),
:global(a) {
  appearance: none;
  border-radius: .5rem;
  padding: .5rem;
  border-width: 1px;
  border-style: solid;
  margin: .5rem 0;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box; }

.offset {
  margin-left: -.5rem; }

.faint {
  opacity: .6; }

.button {
  appearance: none;
  text-decoration: none;
  color: var(--color-white);
  padding: 0 1rem;
  border-radius: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-gray);
  border-width: 0;
  height: 3rem; }
  .button > span {
    margin-right: .5rem; }
  .button:hover {
    cursor: pointer; }

.app {
  margin: 1rem auto;
  padding: 2rem;
  border-radius: 1rem;
  width: 100%;
  max-width: 750px;
  box-shadow: var(--shadow);
  color: var(--color-text);
  background: var(--color-white);
  position: relative;
  box-sizing: border-box; }

.intro {
  margin-bottom: 3rem; }

.link-field {
  width: 100%; }

.buttons {
  display: flex;
  align-items: center;
  margin-top: .25rem;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap; }

.loader {
  text-align: center;
  margin-top: 1rem; }

.preview {
  margin-top: 3rem; }

/*# sourceMappingURL=app.svelte.css.map */</style>

<div class="app">
    <div class="intro">
        <h1>FigSocket</h1>
        <p>Generate a JSON endpoint for any Figma document.</p>
        <h3>Getting Started</h3>
        <ol>
            <li>
                <p>Locate your Figma document URL.</p>
                <img width="100%" src="image/figma-url.png" />
            </li>
            <li>
                <p>Confirm your document share permissions are set to public.</p>
                <img width="100%" src="image/screenshot.png" />
            </li>
        </ol>
    </div>
    <div class="content offset">
        <form on:submit|preventDefault="{generateEndpoint}">
            <input
                class="link-field"
                type="text"
                bind:value="{url}"
                placeholder="Document URL"
                disabled={endpoint || loading}
            />
        </form>
        <div class="buttons">
            {#if !loading && !endpoint}
                <button class="button" on:click="{generateEndpoint}">Generate Endpoint</button>
                <button class="button faint" on:click="{runDemo}">Run Demo</button>
            {/if}
            {#if endpoint}
                <a class="button bg-green" href="{url}" target="_blank">
                    <span>
                        <img height="15" src="image/figma.png" />
                    </span>
                    Figma Document
                </a>
                <a class="button bg-gray" href="{endpoint}" target="_blank">
                    <span>
                        &#123; &#125; 
                    </span>
                    JSON Endpoint
                </a>
            {/if}
        </div>
    </div>
    {#if loading}
        <div class="loader">
            <img src="image/loader.svg" height="20"/>
        </div>
    {/if}
    
    {#if endpoint}
        <div class="preview">
            <h3>JSON Preview</h3>

            {#if !preview}
                <div class="loader">
                    <img src="image/loader.svg" height="20"/>
                </div>
            {:else}
                <Preview {preview}/>
            {/if}
        </div>
    {/if}
</div>

<script>
    import Preview from "./shared/components/preview.svelte";

    let url = "";
    let error = false;
    let loading = false;
    let preview = false;
    let endpoint = false;

    const generateEndpoint = async () => {
        error = false;
        loading = true;

        try {
            const response = await fetch(`/api/generate-endpoint?url=${encodeURIComponent(url)}`);

            ({ endpoint } = await response.json());
            loading = false;

            return fetchPreview()
        } catch(error) {
            console.log(error);
            error = true;
            loading = false;
        }
    }

    const fetchPreview = async () => {
        const response = await fetch(endpoint);

        const json = await response.json();

        return (preview = JSON.stringify(json, null, 4));
    }

    const runDemo = () => {
        loading = true;

        url = "https://www.figma.com/file/EoIGjb8EwKbqjw7TRq5JUg/FigSocket?node-id=0%3A1";
        setTimeout(generateEndpoint, 500);
    }

    const resetState = () => {
        error = false;
        loading = false;
        endpoint = false;
    }
</script>
